
import {mapState,mapMutations,mapActions } from 'vuex';
import Swiper from 'vux/src/components/swiper/swiper.vue'

export default {
name: 'bystagesDetails',
data () {
return {
isOk:false,
minfo:{
name:"华山整形",
descPic:[],
description:""
}
}
},
components: {
Swiper
},
created(){

if(this.userInfo==null){

this.getUserinfo().then(response => {
console.log(11)
this.getHandle();
}).catch(err => {
console.log(11)
this.errorEnv(err)
this.$vux.alert.show({
title: '出错提示',
content: this.errorText||'获取个人信息出错！'
});
});
}else{
console.log(22)
this.getHandle();
}
console.log(this.$route.params.mId)
this.UpdateAuthMerges();
this.$ajax({
method: 'get',
url:'/static/test/merchant.json', //<---本地地址

}).then(response=>{
console.log(response)
this.minfo=response.data[0]
}).catch(function(err){
this.errorEnv(err);
this.$vux.alert.show({
title: '提示',
content: this.errorText
});
})











},
mounted(){

},
computed:{
...mapState(['errorText','authMerges','userInfo','orderInfo']),
picList(){
const pics=this.minfo.descPic.map(function (item) { return {"img":item} });
return pics;
}
},
methods:{
...mapMutations(['errorEnv','UpdateAuthMerges']),
...mapActions(['getUserinfo','getOrderinfo']),
getHandle(){
const info = this.userInfo;
if(info.stepState==0||info.stepState==2){
this.isOk=true;
}
}
}
}



